@import url(https://fonts.googleapis.com/css2?family=Potta+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.head-title {
    margin: 0px;
    vertical-align: top;
    padding: 0px 0px 0px 0px;
    border-bottom: 1px solid #1a183a;
    background-color: #202020;
}

.head-title h3{
    font-family: 'JosefinSans', sans-serif;
    margin: 0px;
    padding: 0px;
    color: white;
}
.footer-img {
    width: 100%;
    max-width: 1400px;
}
.app-compute {
  min-width: 500px;
  padding: 0px 10px 10px 10px;
  margin-left: 0px;
}

.app-weapon-list {
  text-align: center;
}

.App {
  display: flex;
  margin-top: 15px;
  margin-left: 5px;
  margin-right: 5px;
  margin-bottom: 15px;
}

@-webkit-keyframes dropDn-open{
  0% {
    display: none;
    opacity: 0;
  }

  1% {
    display: block;
    opacity: 0;
  }

  100% {
    display: block;
    opacity: 1;
  }
}

@keyframes dropDn-open{
  0% {
    display: none;
    opacity: 0;
  }

  1% {
    display: block;
    opacity: 0;
  }

  100% {
    display: block;
    opacity: 1;
  }
}

.dropDn-p {
  display: block;
}

.dropDn-c {
  display: none;
  opacity: 0;
}

.dropDn-p:hover .dropDn-c{
  display: block;
  opacity: 1;
  -webkit-animation: dropDn-open 0.3s ease-in 0s forwards;
          animation: dropDn-open 0.3s ease-in 0s forwards;
}

.app-equiped{
  display: flex;
  flex-flow: column;
  min-width: 100px;
  margin-right: 10px;
  margin-left: 10px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.app-summons{
  display: flex;
  border: 0px solid #ccc;
  width: 290px;
}

.summons-button{
  display: flex;
  flex-flow: column;
  width: 75px;
  font-size: 11px;
  text-align: center;
  margin: 1px;
  padding: 3px;
}

.summons-button:hover{
  background-color: #282c34;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}



.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.button {
    background-color: #4CAF50; /* Green */
    border: none;
    color: white;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
  }
